import * as React from 'react'

type Props = {
    data: string[]
}

const Twitter: React.FunctionComponent<Props> = ({ data }) => (
    <div>
        {data.map((item, index) => (
            <blockquote className="twitter-tweet"><a href="https://twitter.com/WSJ/status/1235692304624685056">link</a></blockquote>
        ))}
    </div>
)

export default Twitter